.jumbotron {
  background-image: url("https://cdn.pixabay.com/photo/2017/06/27/13/53/books-2447391_1280.jpg");
  opacity: 0.75;
  color: white;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 450px !important;
  width: 100%;
  padding-top: 10;
  text-align: center;
  font-family: 'Merriweather', serif;
  border-radius: 0;
  /* margin-top: -6vh; */
}

.jumboTitle,
.jumboText {
  text-shadow: 2px 3px 4px #000000;
}

* {
  font-family: 'Montserrat', sans-serif;
  color: #636d68;
  }

.card {
  width: 100%;
  padding-bottom: 2vh;
  border: 2px solid #636d68;
}

.card-image {
  padding-left: 5vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.card-title {
  font-family: 'Merriweather', serif;
  color: #636d68;
}

.btnRow {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.deleteBook {
  margin-left: 10px;
  background-color: #608773;
  color: white;
}

.backToSearchBtn {
  margin-left: 5px;
  background-color: #C47E9E !important;
  color: white
}

.backToSearchBtn:hover {
  margin-left: 5px;
  background-color: #C47E9E !important;
  color: white;
  font-size: 1.05rem;
}

.backToSearchBtn:active {
  color: white !important;
  font-size: 1rem;
}
.navbar {
  background-color: #636D68;
  font-family: 'Montserrat', sans-serif;
}

.navbar-brand {
  color: white;
  cursor: default;
}

#btnSaved,
#btnSearch {
  color: white;
}

#btnSaved:hover,
#btnSearch:hover {
  font-size: 1.05rem;
}

#btnSaved:active,
#btnSearch:active {
  font-size: 1rem;
}


form {
  border-radius: 5px;
  border: 2px solid #636d68;
}

.searchTitle {
  margin-left: 2vw;;
  margin-top: 2vh;
  color: #636d68;
  text-align: center;
}

.submitBtn {
  background-color: #608773 !important;
  color: white;
}

.submitBtn:hover {
  color: white;
  font-size: 1.05rem;
}

.submitBtn:active {
  color: white !important;
  font-size: 1rem;
}

.input {
  width: 90%;
}


@media (max-width: 991px) {
  .searchTitle {
    margin-left: 3.5vw !important;
    margin-right: 3.5vw !important;
  }
}

